.CheckCircle {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 22px;
    height: 22px;
    border-radius: 50%;
    border: 1px solid var(--GrayLine);
    cursor: pointer;
}

.CheckCircle-active {
    background-image: linear-gradient(150deg, hsl(192, 100%, 67%), hsl(280, 87%, 65%));
}